<template>
  	<el-row :gutter="20" class="container">
			<el-col :span="12" :offset="7">
			  	<el-main>
			    	<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
					  <el-form-item label="用户账号" prop="account">
					   		<el-input type="text" v-model="ruleForm2.account" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item label="手机号" prop="phone">
					   		<el-input type="text" v-model="ruleForm2.phone" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item label="密码" prop="pass">
					     <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item label="确认密码" prop="repass">
					     <el-input type="password" v-model="ruleForm2.repass" auto-complete="off"></el-input>
					  </el-form-item>
					  <el-form-item prop="imgValidCode">
					     <el-input type="text" v-model="ruleForm2.imgValidCode" class="fl cl-input" auto-complete="off" placeholder="图形验证码"></el-input>
					     <img :src="img_url" class="fr valid-code-icon" @click="getImgCode"/>
							 <!-- @/assets/imgs/code.jpg -->
					  </el-form-item>
					  <el-form-item prop="phoneValidCode">
					     <el-input type="text" v-model="ruleForm2.phoneValidCode" class="fl cl-input" auto-complete="off" placeholder="手机验证码"></el-input>
					    	<el-button type="primary" class="fr w140" :disabled="mobileCode.btnStatus" @click="getMobileCode"><span v-text="mobileCode.validcode"></span><span class="ml5" v-text="mobileCode.btnStatus?'秒':''"></span></el-button>
					  </el-form-item>
					   <el-form-item><!--<router-link to="/register/stepSec" class="a-link"></router-link>-->
					   	<el-button type="primary" @click="next('ruleForm2')" class="btn0" :disabled="isDisabled">下一步</el-button>
					  </el-form-item>
					</el-form>
				</el-main>	
		  	</el-col>
		</el-row>
</template>
<script>
export default {
  data() {
	  var validAccount = (rule, value, callback) => {
	  	if (!value.match(/\S/)) {
          return callback(new Error('用户账号不能为空'));
        } 
        if(!value.match(/^[a-zA-Z0-9\-\_\\()]{4,20}$/)) {
        	return callback(new Error('4-20个字符，支持文字、英文（区分大小写），支持数字“_” “-”、()的组合'));
        }
		return callback();
	  }
	  
	  var validPhone = (rule, value, callback) => {
	  	if (!value.match(/\S/)) {
          return callback(new Error('手机号码不能为空'));
        } 
        if(!value.match(/^((\+86\-?)|(\(\+86\)))?1[34578][0-9]{9}$/)) {
          return callback(new Error('请填写正确的手机号码'));
        }
		return callback();	
	  }
	   
      var validPass = (rule, value, callback) => {
       if (!value.match(/\S/)) {
          return callback(new Error('密码不能为空'));
        } 
        if(!value.match(/^[^\u4e00-\u9fa5\s]{6,20}$/)) {
          return callback(new Error('6-20个字符，由数字、英文字母（区分大小写）或符号两种以及以上的组合，不允许有空格'));
        }
		return callback();
      };
      
      var validRepass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      
      return {
      	isDisabled: false,
        ruleForm2: {
          account: '',
          phone: '',
          pass: '',
          repass: '',
          imgValidCode: '',
          phoneValidCode: ''
        },
        rules2: {
          account: [
          	{validator: validAccount, trigger:'blur'}
          ],
          phone: [
          	{validator: validPhone, trigger:'blur'}
          ],
          pass: [
            {validator: validPass, trigger:'blur'}
          ],
          repass: [
            {validator: validRepass, trigger:'blur'}
          ],
          imgValidCode: [
          	{required: true, message: '请填写图形验证码', trigger: 'blur'}
          ],
          phoneValidCode: [
          	{required: true, message: '请填写手机验证码', trigger: 'blur'}
          ]
        },
        mobileCode: {
        	btnStatus: false, 
        	validcode: '获取手机验证码'
				},
				img_url: ''
      };
	},
	methods: {
		//下一步
		next(formName) {
			this.isDisabled = true;
			 this.$router.push({"path": "/register/stepSec", params: { userId: 123 }})
			this.$refs[formName].validate((valid) => {
				this.isDisabled = false;
	          if (valid) {
	            alert('submit!');
	           
	          } else {
	            console.log('error submit!!');
	            return false;
	          }
	        });
		},
	    addNewTask() {
	        this.$store.dispatch('postNewTask', {title: this.newValue}).then(response => {
	            this.newValue = '';
	            alert('添加成功');
	        }).catch(err => {
	            alert('添加失败：' + err.data.data);
	        });
	    },
	    //触发按钮，引发回调函数
	    timeCallback(opt) {
	    	var that = this;
	    	var obj = opt || {};
	    	that.mobileCode.btnStatus = true;
	    	that.mobileCode.validcode = opt.time;
            function timeCount() {
                if(that.mobileCode.validcode <= 1) {
                    clearInterval(t);
                    that.mobileCode.validcode = obj.str;
                    that.mobileCode.btnStatus = false;
                } else {
                    that.mobileCode.validcode -= 1;
                }
            }
            var t = setInterval(timeCount, 1000);
	    },
	    //获取验证码
	    getMobileCode() {
	    	var opt = {
               str: '重新获取验证码',
               time: 59
						};
				this.timeCallback(opt);
				let installation_ID = this.Cookie.getCookie('installation_ID');
				let user_mobile = this.ruleForm2.phone;
				if(user_mobile == '') {
					alert("请先输入手机号")
					return false;
				}
				
					this.$store.dispatch('getMobileCode', {"installation_ID": installation_ID, "user_mobile": user_mobile}).then(response => {
	            alert('获取手机验证码成功');
	        }).catch(err => {
	            alert('获取图片失败：' + err.data.data);
	        });
			},
			getImgCode() {
					let installation_ID = this.Cookie.getCookie('installation_ID');
					this.$store.dispatch('getImgCode', {"installation_ID": installation_ID}).then(response => {
	            alert('获取图片成功');
	        }).catch(err => {
	            alert('获取图片失败：' + err.data.data);
	        });
			}
	},
	created() {
		console.log(this.$router.params)
	}
}
</script>


